.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.movies {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.movie {
  width: 23%;
  margin: 5px;
  padding: 5px;
}

.hidden {
  display: none;
}

.movie:hover .hidden {
  display: block;
}
.input {
  width: 100%;
}
